import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { MenuComponent }  from './main/menu/menu.component';
import { GuestComponent }  from './main/menu/guest/guest.component';
import { MusicComponent }  from './main/menu/music/music.component';
import { GameComponent }  from './main/menu/game/game.component';
import { BeerComponent }  from './main/menu/beer/beer.component';
import { GamecategoriesComponent }  from './main/menu/gamecategories/gamecategories.component';


const routes: Routes = [
	{ path: '', component: MenuComponent },
	{ path: 'guest', component: GuestComponent },
	{ path: 'music', component: MusicComponent },
	{ path: 'game', component: GameComponent },
	{ path: 'beer', component: BeerComponent },
	{ path: 'categories', component: GamecategoriesComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
