import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import { Observable } from 'rxjs';
import { Data } from './data-type';
import { Text } from './text-type';
import { End } from './endgame-type';

@Injectable({
  providedIn: 'root'
})
export class GameService {
	

  	constructor(
  		 private http: HttpClient
  	) { }

  	private getUrlStatus = 'http://37.57.31.220:3000/api/rooms/1/status';
  	private getUrlStartGame = 'http://37.57.31.220:3000/api/rooms/1';
    private getUrlCategories = 'http://37.57.31.220:3000/api/rooms/1/questions/categories/category_1';
    private getUrlEndGame = 'http://37.57.31.220:3000/api/rooms/1/end';


  	getStatus(): Observable<Data[]> {
  		return this.http.get<Data[]>(this.getUrlStatus, {responseType: "json"});
	  }	
	  getStartGame(): Observable<Text[]> {
  		return this.http.get<Text[]>(this.getUrlStartGame, {responseType: "json"});
	  }	
    getCategories(): Observable<Text[]> {
      return this.http.get<Text[]>(this.getUrlCategories, {responseType: "json"});
    }  
    postEndGame(): Observable<any> {
      return this.http.post(this.getUrlEndGame, 
      {
        isFrontend: true
      });
    }  
}
